import React, { Component } from 'react'
import { NoSlider } from './general/style/style';
import { NoSliderItem } from './general/style/style';

export default class NoSliderComp extends Component {

  render() {
   // console.log("NoSliderComp: ",this.props.noOfItems);
    let noOfItems;
    try {
      noOfItems = this.props.noOfItems || this.props.children.length;     
    } catch (err) {
    console.log(err);
    }
    return (
      <>
      {<NoSlider margin="0 0 16px 0" padding={this.props.padding} style={this.props.style} noOfItems={this.props.noOfItems}>
      {this.props.children && React.Children.map(this.props.children.slice(0, noOfItems), (child, index) => (
        <>
          <NoSliderItem 
            className={this.props.className ? this.props.className : ''}
            margin={this.props.margin}
            noOfItems={this.props?.noOfItems}
          >
            {React.cloneElement(child, { isFallbackCase: true })}
          </NoSliderItem>
        </>
      ))}
    </NoSlider>}
    </> 
    )
  }
}







